@import "@/styles/config";




























































































































































































































































































































$card-gap: $spacing-container / 2;

#app {
  overflow: hidden;
  padding: $spacing-container;
}
.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: -$card-gap;
}
.card {
  max-width: 250px;
  margin: $card-gap;
}
