@import "@/styles/config";












































































































































































































































$spacing: $spacing-3;
$top: 12%;
$left: 7%;
$height: calc(100% - #{$top});
$width: calc(100% - #{$left});

.card {
  width: 100%;
  border-radius: 15px;
  transition: border-radius $transition;
}
.card-inner {
  position: relative;
  padding-top: 100%;
  border-radius: inherit;
}
.card-inner-2 {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border-radius: inherit;
}

.background {
  position: absolute;
  z-index: -1;
  width: $width;
  height: $height;
  border-radius: inherit;
  background-image: linear-gradient(
    45deg,
    color(delta) 30%,
    color(epsilon) 100%
  );
  top: $top;
  left: $left;
  display: block;
}

.content-wrapper {
  height: $height;
  width: $width;
  display: flex;
  border-radius: inherit;
}
.content {
  position: relative;
  flex-grow: 1;
  background-color: white;
  border-radius: inherit;
  transition-timing-function: $transition-timing-function;
  overflow: hidden;
  background-image: linear-gradient(to top, rgba(color(epsilon), 0.5), white);
  display: flex;
  align-items: flex-start;
}

.loader,
.loader-space-saver-wrapper {
  margin: auto;
  width: 50%;
  max-width: 500px;
}
.loader-space-saver-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
}
.loader-space-saver-wrapper {
  display: flex;
  align-items: center;
}
.loader-button,
.loader-space-saver {
  display: block;
  width: 100%;
  padding-top: 100%;
  position: relative;
  border-radius: 5px;
}
.loader-holder {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: inherit;
}

.toolbar {
  z-index: 2;
  transition: opacity $transition;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  transition: fill $transition;
  > * {
    padding: $icon-padding;
    > * {
      height: $icon-size;
      width: $icon-size;
    }
  }

  &:hover {
    opacity: 1;
    pointer-events: auto;
  }
}

@include breakpoint-desktop {
}
