@import "@/styles/config";

























































































































.code-modal {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  will-change: background-color;
  z-index: 3;
}

.modal-container {
  width: 100%;
  max-width: 800px + ($spacing-container * 2);
  display: flex;
}
.modal-wrapper {
  width: 100%;
  margin-top: auto;
  margin-bottom: auto;
  padding: $spacing-container;
}
.modal {
  position: relative;
  width: 100%;
  background-color: white;
  border-radius: 15px;
  opacity: 1;
  will-change: transform, opacity;
}

.top {
  display: flex;
  justify-content: flex-end;
}
.close {
  padding: $spacing-2;
}
.cross {
  height: $icon-size;
  width: $icon-size;
}

.content {
  padding: 0 $spacing-container $spacing-container $spacing-container;
}
.credit {
  margin-bottom: $spacing-3;
}
.credit-url {
  text-decoration: underline;
}
.source {
  &:not(:last-child) {
    margin-bottom: $spacing-3;
  }
}

@include breakpoint-mobile {
  .credit,
  .source {
    font-size: $font-1;
  }
}
