@import "@/styles/config";

















































.source {
  position: relative;
  display: block;
  background-color: #eee;
  border-radius: 5px;
  font-size: $font-2;
  padding: $spacing-3;
}
.copy-wrapper {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
}

.code {
  word-break: break-word;
  white-space: pre-wrap;
  font-family: courier, monospace;
}
.copied {
  color: white;
  padding: $spacing-1;
  background-color: color(beta);
  border-radius: 10px;
  transition: opacity $transition, transform $transition;
  display: flex;
  align-items: center;

  &:not(.visible) {
    transform: translateX(10%);
    opacity: 0;
  }
}

@include breakpoint-desktop {
  .button {
    padding: $spacing-3;
  }
  .copy {
    height: $icon-size * 2;
    width: $icon-size * 2;
  }
}
@include breakpoint-mobile {
  .button {
    padding: $spacing-2;
  }
  .copy {
    height: $icon-size * 1.25;
    width: $icon-size * 1.25;
  }
}
